import { vmData } from './../../valueMappingData';
import { DipValueMappingModel } from './../model/DipValueMappingModel';
import { isNullOrUndefined } from 'util';
import { isEmpty } from 'rxjs/operators';
import { isNullOrEmptyString } from '@progress/kendo-angular-grid/dist/es2015/utils';
import { DipVmColumnModel } from '../model/DipVmColumnModel';

export class ValueMappingValidator {

}
/**
 * CheckValueMapping
 */
export function CheckValueMapping(valueMapping: DipValueMappingModel): boolean {
    // 
    if (IsEmptyValidator(valueMapping.id)) {
        alert('');
        return false;
    }
    // 
    if (IsEmptyValidator(valueMapping.code)) {
        alert('');
        return false;
    }
    // 
    if (IsEmptyValidator(valueMapping.name)) {
        alert('');
        return false;
    }
    // 
    if (valueMapping.defaultStrategy === 3 && IsEmptyValidator(valueMapping.defaultValue)) {
        alert('!');
        return false;
    }
    // 
    if (valueMapping.defaultStrategy === 4 && IsEmptyValidator(valueMapping.defaultValue)) {
        alert('!');
        return false;
    }
    // 
    if (valueMapping.code.length > 32) {
        alert('32!');
        return false;
    }
    // 
    if (valueMapping.name.length > 64) {
        alert('64!');
        return false;
    }
    // 
    if (IsEmptyValidator(valueMapping.defaultValue) === false && valueMapping.defaultValue.length > 64) {
        alert('64!');
        return false;
    }
    // 
    if (CodeValidate(valueMapping.code) === false) {
        alert('!');
        return false;
    }
    // 
    if (NameValidate(valueMapping.name) === false) {
        alert('!');
        return false;
    }
    return true;
}
/// <summary>
/// (,,)
/// </summary>
/// <param name="vmColumn"></param>
/// <returns></returns>
export function CheckDipVmColumn(vmColumn: DipVmColumnModel) {
    // 
    if (IsEmptyValidator(vmColumn.id)) {
        alert('!');
        return false;
    }

    // 
    if (IsEmptyValidator(vmColumn.colText)) {
        alert('!');
        return false;

    }

    // 
    if (vmColumn.colText.length > 64) {
        alert('64!');
        return false;
    }

    // 
    if (NameValidate(vmColumn.colText) === false) {
        alert('!');
        return false;
    }
    return true;
}

/// <summary>
/// ColText,(,,)
/// </summary>
/// <param name="vmColumnList"></param>
export function CheckVmColumnListRepeat(vmColumnList: DipVmColumnModel[]): boolean {
    for (const key in vmColumnList) {
        if (vmColumnList.hasOwnProperty(key)) {
            const vmColumn = vmColumnList[key];
            if (IsEmptyValidator(vmColumn.vmId)) {
                alert('vmid!');
                return false;
            }
            // 
            if (IsEmptyValidator(vmColumn.colText)) {
                alert('!');
                return false;

            }

            // 
            if (vmColumn.colText.length > 64) {
                alert('64!');
                return false;
            }

            // 
            if (NameValidate(vmColumn.colText) === false) {
                alert('!');
                return false;
            }
            const count = vmColumnList.filter(vmc => vmc.colText === vmColumn.colText).length;
            if (count > 1) {
                alert('' + vmColumn.code + '!');
                return false;
            }
        }
    }
    return true;
}

/// ColText

export function CheckValueMappingListRepeat(valuemappingList: DipValueMappingModel[]): boolean {
    for (const key in valuemappingList) {
        if (valuemappingList.hasOwnProperty(key)) {
            const valuemapping = valuemappingList[key];
            if (IsEmptyValidator(valuemapping.id)) {
                alert('id!');
                return false;
            }
            const codeCount = valuemappingList.filter(vm => vm.code === valuemapping.code).length;
            if (codeCount > 1) {
                alert('' + valuemapping.code + '!');
                return false;
            }
            const nameCount = valuemappingList.filter(vm => vm.name === valuemapping.name).length;
            if (nameCount > 1) {
                alert('' + valuemapping.name + '!');
                return false;
            }
        }
    }
    return true;
}
export function CheckCode(code: string): Boolean {
    if (isNullOrUndefined(code) || isNullOrEmptyString(code)) {
        alert('');
        return false;
    }
    if (code.length > 32) {
        alert('32');
        return false;
    }
    return true;
}

export function CheckName(name: string): Boolean {
    if (isNullOrUndefined(name) || isNullOrEmptyString(name)) {
        alert('');
        return false;
    }
    if (name.length > 64) {
        alert('64');
        return false;
    }
    return true;
}

export function IsEmptyValidator(name: string): Boolean {
    if (isNullOrUndefined(name) || isNullOrEmptyString(name)) {
        return true;
    }
    return false;
}
/// <summary>
/// ()
/// </summary>
/// <param name="code"></param>
/// <returns></returns>
export function CodeValidate(code: string): boolean {
    const reg = new RegExp('^[a-zA-Z0-9_]+$');
    const arr = code.match(reg);
    if (arr == null) {
        return false;
    }
    if (arr.length === 1) {
        return true;
    } else {
        return false;
    }
}
/// <summary>
/// ()
/// </summary>
/// <param name="name"></param>
/// <returns></returns>
export function NameValidate(name: string): boolean {
    const reg = new RegExp('^[a-zA-Z0-9\u2E80-\u9FFF_]+$');
    const arr = name.match(reg);
    if (arr == null) {
        return false;
    }
    if (arr.length === 1) {
        return true;
    } else {
        return false;
    }
}

